.projectsBlock {
    min-height: 100vh;
    background-color: rgb(254, 255, 204);
    
}
.projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
}
.title {
    background-color: rgba(255, 169, 71, 0.795);
}
.projects{
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    box-sizing: border-box;
}
