.button {
    
    font-weight: 700;
    color: white;
    text-decoration: none;
    padding: .8em 1em calc(.8em + 3px);
    border-radius: 3px;
    background: rgb(81, 53, 85);
    box-shadow: 0 -3px rgb(33, 34, 34) inset;
    transition: 0.2s;
    border-radius: 10px;
    } 
.button:hover {
    background: rgb(186, 129, 194);
    }
.button:active {
    background: rgb(247, 178, 255);
    box-shadow: 0 3px rgb(33, 34, 34) inset;
    }
